

var s = "helloworld"
var nRows = 4

function printZigZag() {
	var n = nRows - 1
	var offset = 0
	var result = ""
	var length =  s.length
	var consumed  = 0

	for (let r=0; r < nRows; r++) {
		offset = r

		while (offset < length) {
			result += s.charAt(offset)

			// column index from a given offset
			var cidx = (n - r)
			if ((r > 0) && (length >=  (offset + cidx))) {
				result += s.charAt(offset+cidx)
			}

			offset += 2 * n
		}
	}

	return result
}

var result = printZigZag()
console.log(result)
